        p.mb-0 Diseo viene del italiano disegnare que significa dibujar, que a su vez se deriva del latn designare que evoca marcar, trazar, ordenar y disponer. El diseo es una actividad creativa orientada a soluciones idneas a ciertas problemticas.
    
