        p.mb-0 La definicin viene del francs mode, y ste del latn modus, "modo" o "medida". La moda regula las elecciones que hacen las personas, definiendo ciertos patrones de comportamiento dentro de un grupo social; indica a la gente qu debe consumir, utilizar o hacer.
    
