    p.small.mb-5 Perfil y estilo de consumidor
 
    p.mb-6 Para el anlisis de una marca o mercado, se requiere de la categorizacin de grupos de personas con caractersticas similares, estudio que permitir identificar su comportamiento, consumo e intereses, permitiendo as que el producto o servicio a ofrecer sea mucho ms especfico y certero con el cliente o nicho de mercado.

    .tarjeta.tarjeta--amarillo-claro.mb-6
      .tarjeta--amarillo-claro__titulo
        .h11 Quin es el<br>consumidor? 
      .row
        .col-lg-4.col-xl-6.mb-5
        .col-lg-8.col-xl-6
          p.mb-0 Se entiende por consumidor al individuo que requiere satisfacer cierta necesidad a travs de la compra u obtencin de determinados productos. 
            strong El consumidor no es un simple agente PASIVO que espera a que le ofrezcan productos, es un agente ACTIVO con el poder suficiente para lograr cambios 
            | en las ofertas y hasta en las mismas empresas, para que se ajusten a sus requerimientos y necesidades.


    .h1.mb-5 Perfil del consumidor

    p.mb-5 Es la descripcin de las caractersticas de los consumidores de un producto o usuarios de un servicio determinado y un modelo de pensamiento. Es UNA de las variables para la definicin de cualquier estrategia de marketing.
      br
      | El perfil NO se determina SLO por la edad NI por el segmento socio econmico.

    .tarjeta.tarjeta--lavanda.mb-6
      Slyder(clase-tarjeta="tarjeta--crema")
        .row.justify-content-center.align-items-center
          .col-4.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img43.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Tradicional
            p.mb-0 Es un consumidor que participa en un ambiente social exigente en el que busca reconocimiento, aceptacin y afirmacin de los dems; invierte mucho dinero, tiempo y energa en sobresalir y destacarse.
        
          .col
            .h1 Caractersticas
            ul.mb-0
              li Las tradiciones permanecen.
              li Es rgido, formal y exigente con la etiqueta y el protocolo.
              li Presenta resistencia al cambio.
              li Mantiene su estilo a travs del tiempo.
              li Es reservado y se toma tiempo para involucrar nuevos conceptos y productos innovadores.
              li Busca siempre un sello de tradicin y distincin.
              li Es fiel a las marcas, busca en ellas el estatus.
              li Mantiene y aprecia los referentes del pasado.

        .row.justify-content-center.align-items-center
          .col-4.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img44.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Neo tradicional
            p.mb-0 Es un consumidor evolucionado y sintonizado con el desarrollo del entorno. Tiene un mayor nivel de sensibilidad y flexibilidad. Se detiene a observar y se deja influenciar por nuevos referentes, conceptos y propuestas de moda. Mantiene un punto de equilibrio entre la tradicin y la innovacin, integrando los fenmenos de la moda, la evolucin cultural y social a su estilo personal.
        
          .col
            .h1 Caractersticas
            ul.mb-0
              li Las tradiciones permanecen.
              li Conserva las normas.
              li Mantiene el gusto y aprecia la etiqueta y el protocolo, pero no las maneja con rigor.
              li Es fiel a las celebraciones, ritos y rutinas.
              li Involucra fcilmente nuevos conceptos y productos innovadores.
              li Busca siempre un sello de tradicin y distincin.
              li Fiel a las marcas, busca en ellas el estatus.
              li Aprecia los referentes actuales.

        .row.justify-content-center.align-items-center
          .col-4.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img45.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Contemporneo
            p.mb-0 Surge de los cambios sociales, econmicos y tecnolgicos de las ltimas dcadas, dando origen a un nuevo modelo de comportamiento, donde se encuentran un perfil original y autntico que est en permanente evolucin.
              br
              br
              | Es multicultural, dinmico y explorador constante de su propio desarrollo personal, emocional y profesional.
        
          .col
            .h1 Caractersticas
            ul.mb-0
              li Evoluciona y modifica sus valoraciones.
              li Tiene definido su propio estilo personal.
              li El desarrollo personal y profesional est por encima de lo familiar.
              li La mujer juega un rol profesional, laboral, poltico y familiar.
              li Sensible al cambio, adopta fcilmente nuevas alternativas para estar en constante evolucin.
              li Es dinmico.
              li Es citadino y urbano.

        .row.justify-content-center.align-items-center
          .col-4.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img46.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Vanguardista
            p.mb-0 Es un destacado grupo social inconforme y extremadamente original. Genera cambios y origina nuevos conceptos, es innovador, rompe esquemas y paradigmas de su entorno directo generando cambios que se reflejan como tendencias globales. multicultural, dinmico y explorador constante de su propio desarrollo personal, emocional y profesional.
        
          .col
            .h1 Caractersticas
            ul.mb-0
              li Cambia y modifica fcilmente su escala de valoraciones.
              li No tiene apegos ni crea compromisos con ningn formato.
              li Vive pendiente de lo que surge en las grandes capitales del mundo.
              li La innovacin es parte de su vida diaria, es cambiante e inestable.
              li Lo motiva explorar referentes en otras culturas para convertirse en habitante planetario.
              li Su vida viaja a la velocidad del Internet.
              li Todo es recibido, aceptado, permitido y adoptado tal vez solo por hoy.
              li Es atrevido, le gusta el juego, el riesgo, lo novedoso.

        .row.justify-content-center.align-items-center
          .col-4.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img47.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Exhibicionista
            p Resulta de un deseo personal por destacarse, llamar la atencin y proyectarse como distinto a los dems. El estilo es lo de menos, la imagen es todo.
        
          .col
            .h1 Caractersticas
            ul.mb-0
              li Quiere llamar permanentemente la atencin y ser distinto de los dems.
              li Rompe todos los esquemas para no pasar desapercibido.
              li Concentra su atencin en todas aquellas rarezas y excentricidades que el mercado le ofrece o de las que l mismo se apropia.
              li Prefiere proyectar una imagen nueva a regirse por un canon establecido.
              li Su apariencia personal es original e innovadora pero extremadamente llamativa.



    .tarjeta.tarjeta--amarillo.tarjeta--comillas.mb-6
      .h1.mb-0 El estilo personal es un proceso creativo constante y atractivo, que evoluciona el ser.
        br
        br
        | No tiene reglas, excepto las que uno se ponga; las cuales tambin pueden ser ajustadas.
      

    .h1.mb-5 Estilos del consumidor

    p.mb-6 Coherencia entre el ser y el parecer. La relacin que tiene el modelo de pensamiento y la actitud personal define el estilo de cada individuo. 
      strong El estilo refleja todos los aspectos de una persona, el pensamiento, las acciones y la relacin con los dems y los objetos, adems sus gustos, preferencias, motivaciones, valoraciones necesidades y actividades reflejadas en sus actitudes

    .tarjeta.tarjeta--amarillo-claro.mb-6
      .tarjeta--amarillo-claro__titulo
        .h11 Quin es <br>el estilo? 
      .row
        .col-lg-4.mb-5
        .col-lg-8
          p.mb-0 Es una seleccin y acumulacin de vivencias, referentes, culturas e influencias que se prioriza, valora y manifiesta con nuestra actitud y expresin a travs de los objetos que elegimos para llevar en nuestra indumentaria y nuestro diario vivir, lo que se contribuye en la coherencia en el habitus, hbito, hbitat que determinar un estilo personal, particular y plural.


    .tarjeta.tarjeta--crema.mb-5
      Slyder(clase-tarjeta="tarjeta--lavanda")
        .row.justify-content-between.align-items-center
          .col-3.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img50.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Estilo Clsico
            p Obra que se tiene por modelo digno de imitacin; genialidad en la idea o diseo donde el valor y el aprecio permanece en el tiempo y son sensibles a evolucionar.
        
          .col-12.mb-4
            .h1 Caractersticas
            ul
              li Tener el estilo clsico no quiere decir que se ignoren las tendencias de moda.
              li Es tener prendas claves que se editan, que se pueden combinar y que permanecen en el tiempo.

          .col-12
            .tarjeta.tarjeta--amarillo.tarjeta--comillas
              .h1.mb-0 Innovacin! Uno no puede ser innovador por siempre. Yo quiero crear clsicos. 
                br
                br
                | Coco Chanel

        .row.justify-content-between.align-items-center
          .col-3.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img48.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Estilo Bohemio
            p El estilo boho o bohemio, es un estilo originalmente popularizado por los bohemios en la dcada de los 60, un grupo de gente que rechazaba los valores morales tradicionales vivan un estilo de vida nmada. No sola ser considerado de moda por los estndares del momento. Originalmente se usaba por comodidad y para desafiar las normas de vestir tradicionales.
        
          .col-12.mb-4
            .h1 Caractersticas
            ul
              li El estilo bohemio es relajado, confortable y lo ms importante, una expresin personal.
              li Valora la comodidad.
              li Las mujeres optan por vestidos sueltos, tnicas, faldas de volados y pantalones amplios o tipo gaucho.

          .col-12
            .tarjeta.tarjeta--amarillo.tarjeta--comillas
              .h1.mb-0 No se puede tener un estilo bohemio usando prendas ajustadas; esto no funciona. Tiene que tener la posibilidad de moverse, bailar y escalar. 
                br
                br
                | Diane Von Furstenberg

        .row.justify-content-between.align-items-center
          .col-3.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img53.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Estilo Minimalista
            p El concepto naci en los aos sesenta, con la idea de menos es ms, del arquitecto Mies Van Der Rohe, quien propuso un estilo de vida que rescata la importancia de la persona, frente a una sociedad consumista. A grandes rasgos, trata no slo de usar poco, sino de encontrar el equilibrio entre reducir y exagerar en la decoracin, los detalles y las siluetas.
        
          .col-12.mb-4
            .h1 Caractersticas
            ul
              li El estilo bohemio es relajado, confortable y lo ms importante, una expresin personal.El estilo minimalista requiere de disciplina, consistencia y gran atencin por la calidad.
              li La mayora de los minimalistas son metdicos en el momento de vestirse y tienen un gran sentido y conciencia de s mismos.
              li Son muy fieles a algunas marcas que les garantizan cortes y diseos perfectos, telas de alta calidad y tecnolgicas.

          .col-12
            .tarjeta.tarjeta--amarillo.tarjeta--comillas
              .h1.mb-0 En el minimalismo no se trata de abandonar los diseos o los estampados. Yo veo al minimalismo como la filosofa que envuelve el total sentido del balance, conociendo cundo quitar o sustraer algo. Es un vicio por los cortes absolutamente impecables, juegos tranquilos del color siluetas muy fuertes y limpias. 
                br
                br
                | Calvin Klein

        .row.justify-content-between.align-items-center
          .col-3.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img51.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Estilo Street Fashion
            p Es un estilo particular y original que se detecta en las calles de cualquier lugar del mundo, donde las personas asumen la indumentaria de una manera muy personal, no siguen las tendencias de moda, por el contrario, son los que inspiran a los diseadores para crear colecciones.
        
          .col-12.mb-4
            .h1 Caractersticas
            ul
              li Es original y no sigue tendencias.
              li Crea su propia identidad y estilo.
              li Es rebelde.
              li Les interesa la autenticidad de cada persona, antes que lo que est de moda en el mercado.

        .row.justify-content-between.align-items-center
          .col-3.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img52.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Estilo Eclctico
            p Una persona eclctica es aquella que selecciona sus prendas de todos los estilos y la mezcla de una manera nica. No hay una frmula ni elementos favoritos, es una invencin permanente.
        
          .col-12.mb-4
            .h1 Caractersticas
            ul
              li No existen normas, pero son importantes las lneas temticas.
              li Mezclar prendas vintage elegidas con estilo individual.
              li Objetos antiguos elegidos con buen gusto y algo de audacia.
              li Se pueden incorporar objetos exticos, prendas de otras dcadas, accesorios de todo de todo tipo, como si se tratara de un collage.

        .row.justify-content-between.align-items-center
          .col-3.mb-5.d-none.d-md-block
            img(src="@/assets/images/pages/tema1/img49.svg")

          .col-md-8.mb-4.mb-md-5
            .h1 Estilo Nostlgico
            p Es un estilo que vive del recuerdo, del pasado. Es romntico y tiene en su esencia toques vintage y colores pasteles y empolvados.
        
          .col-12.mb-4
            .h1 Caractersticas
