    .h3.small.mb-5 Moodboard - Tableros creativos - Cuadro metamrfico


    p.mb-6 Los moodboards son tableros creativos que permiten visualizar y conceptualizar ideas para el desarrollo de productos o servicios, consiste en recolectar imgenes que muestran lo que se quiere expresar, esta es una forma implementada en las reas de diseo para dar a conocer las ideas de una manera mucho ms dinmica.

    .tarjeta.tarjeta--amarillo-claro.mb-6
      .tarjeta--amarillo-claro__titulo
        .h11 Qu es un<br>moodboard?
      .row
        .col-lg-4.mb-5
        .col-lg-8
